.card-screen.sport-card {
  background-image: url(../assets/images/bg/football-bg.png); }
.card-screen.music-card {
  background-image: url(../assets/images/bg/music-bg.jpg); }
.card-screen.fashion-card {
  background-image: url(../assets/images/bg/fashion-bg.jpg); }
.card-screen.movie-card {
  background-image: url(../assets/images/bg/movie-bg.jpg); }

.card-screen {
  padding-top: 224px;
  background-repeat: no-repeat;
  background-position: top;
  background-size: 100% 958px;
  position: relative;

  &__content {
    position: relative; }

  &__video {
    position: relative;
    z-index: 1; }

  &__video-content {
    background: $accent-grnt;
    border-radius: 15px; }

  &__video-header {
    position: relative;
    z-index: 1;
    padding: 90px 0 0 81px; }

  &__video-preview {
    position: absolute;
    top: -1px;
    left: 0;
    right: 0;
    max-height: 648px;
    border-radius: 15px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 15px; } }

  &__video-category {
    p {
      font-weight: 400;
      font-size: 30px;
      line-height: 130%;
      color: $white; } }

  &__video-name {
    p {
      font-weight: 400;
      font-size: 85px;
      line-height: 130%;
      color: $white; } }

  &__video-link {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translateX(-50%); }

  &__video-body {
    display: flex;
    justify-content: space-between;
    padding: 425px 86px 50px 60px; }

  &__video-descr {
    max-width: 466px;
    p {
      font-weight: 400;
      font-size: 24px;
      line-height: 120%;
      color: $white;
      margin-bottom: 0; } }

  &__video-footer {
    display: flex;
    justify-content: space-between;
    margin-top: 33px; }

  &__video-author {
    span {
      font-weight: 700;
      font-size: 20px;
      color: $grey; } }

  &__video-view {
    span {
      font-weight: 700;
      font-size: 20px;
      color: $grey; } }

  &__img {
    img:nth-child(1) {
      position: absolute;
      bottom: -20px;
      left: 0; }
    img:nth-child(2) {
      position: absolute;
      bottom: -20px;
      right: 0; } } }
