.stars-news {
  padding: 64px 0 0 0;
  &-banner {
    position: absolute;
    top: 0;
    left: 0; }

  &__content {}

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between; }

  &__country-filter {
    display: flex;
    align-items: center;
    column-gap: 18px; } }

.body-news {
  padding-top: 41px;

  &__box {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    &-item:nth-child(1) {
      max-width: 626px; } }

  &__title {
    font-weight: 400;
    font-size: 90px;
    line-height: 120%;
    margin-bottom: 40px; }

  &__text {
    font-weight: 400;
    font-size: 33px;
    line-height: 120%; }

  &__box-item .body-news__img {
    width: 386px;
    height: 384px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.1);
    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      object-fit: contain; } }

  &__box-images {
    display: grid;
    grid-template-columns: 182px 182px 385px;
    grid-template-rows: 368px;
    gap: 20px; }

  &__img {
    background: rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px; } }

  &__box-images .body-news__img:first-child {
    align-self: flex-end;
    width: 182px;
    height: 176px; }

  &__box-images .body-news__img:nth-child(2) {
    width: 182px;
    height: 176px; }

  &__box-images .body-news__img:nth-child(3) {
    width: 385px;
    height: 368px; }

  &__adv-img {
    align-self: flex-end;
    max-width: 453px;
    max-height: 408px; } }
