.top-slider-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 87px;
  height: 87px;
  border-radius: 50%;
  border: 1px solid $black;
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  user-select: none;

  .icon--top-arrow {
    width: 22px;
    height: 11px; } }

.top-slider-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 88px;
  height: 87px;
  border-radius: 50%;
  border: 1px solid $black;
  background: transparent;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  user-select: none;

  .icon--top-arrow {
    width: 22px;
    height: 11px;
    fill: $black;
    transform: rotate(180deg); } }

.filter-btn {
  font-weight: 400;
  font-size: 20px;
  color: $black;
  border-radius: 5px;
  border: 1px solid $black;
  background: transparent;
  position: relative;
  z-index: 1;
  padding: 11px 24px;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background: $accent-grnt;
    transition: all 0.3s ease;
    z-index: -1;
    border-radius: 5px;
    opacity: 0;
    visibility: hidden; }
  &:hover {
    border: none;
    padding: 12px 25px;
    color: $white;
    &::after {
      opacity: 1;
      visibility: visible; } } }

.submit {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 293px;
  height: 59px;
  background: $accent-grnt;
  border: none;
  border-radius: 10px;
  font-weight: 400;
  font-size: 21px;
  line-height: 120%;
  color: $white;
  column-gap: 21px;
  .circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34.15px;
    height: 34.15px;
    @include border(50%, 1px, $white); }
  .icon--submit {
    width: 18px;
    height: 14px;
    fill: $white; } }

.favourite-btn {
  border: none;
  background: transparent;
  position: absolute;
  bottom: 13px;
  right: 11px;
  z-index: 1;
  span {
    color: $accent-grnt; }
  .icon--star {
    width: 29px;
    height: 29px;
    fill: $white; } }

.favourite-btn {
  &.fav-active {
    .icon--star {
      fill: #e54631 !important; } } }

.choose-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  column-gap: 35px;
  background: $accent-grnt;
  border: none;
  border-radius: 12px;
  padding: 21.5px 44px;
  span {
    font-weight: 700;
    font-size: 20px;
    color: $white; }
  .icon--choose-btn-arrow {
    width: 52px;
    height: 10px;
    fill: $white; } }

.to-basket {
  font-weight: 700;
  font-size: 22px;
  line-height: 24px;
  color: $white;
  background: $accent-grnt;
  border: none;
  border-radius: 10px;
  margin: 0 auto;
  display: block;
  padding: 28.66px 138.3px 24.59px 122.33px;
  white-space: nowrap; }

.white-btn {
  display: inline-block;
  white-space: nowrap;
  font-weight: 700;
  font-size: 20px;
  background: $white;
  border-radius: 12px;
  padding: 25px 55px;
  color: $black;
  &:hover {
    color: lighten($black, 15%); } }

.circled-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 99px;
  height: 99px;
  border-radius: 50%;
  border: 1px solid $black;
  cursor: pointer;
  user-select: none;
  svg {
    width: 54px;
    height: 11px;
    fill: $black; } }
