.input-wrap {
  margin-bottom: 30px; }

input,
textarea {
  width: 100%;
  font-size: 16px;
  color: $c0;
  padding: 5px 10px;
  border: 1px solid #999;
  transition: all .3s ease;
  &:focus {
    outline: none;
    border-color: $c1; }
  &:disabled {
    background: #fff; } }

::placeholder {
  color: #999; }

.choose-form {

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 82px;

    &-wrap {
      display: flex;
      column-gap: 13px; } }

  &__select.form-select {
    font-family: $deff;
    font-weight: 400;
    font-size: 20px;
    width: 152px;
    height: 58px;
    @include border(5px, 1px, $black);
    background-color: transparent;
    &:hover {
      background: $accent-grnt;
      border: none; }
    &:focus {
      box-shadow: none; } }

  &__option {
    font-family: inherit;
    font-weight: 400;
    font-size: 20px;
    color: $black; }

  &__search-label {
    width: 387px;
    height: 58px;
    position: relative;
    button {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 43px;
      height: 100%;
      border: none;
      background: transparent;
      padding: 0 5px 0 13px;
      svg {
        flex: 0 0 18px;
        height: 18px;
        fill: $black; } } }

  &__search {
    width: 100%;
    height: 100%;
    padding: 18px 15px 17px 42px;
    background: transparent;
    @include border(5px, 1px, $black);
    &::placeholder {
      font-weight: 400;
      font-size: 18px;
      line-height: 130%;
      color: $black; } }

  &__body {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding-bottom: 81px; }

  &__img-label {
    position: relative;
    input {
      display: none; } }

  &__label-content {
    position: relative;
    width: 182px;
    height: 176px;
    border-radius: 10px;
    padding: 4px;
    &.label-active {
      background: $accent-grnt; } }

  &__img-label img {
    width: 100%;
    height: 100%;
    // object-fit: contain
    border-radius: 10px; }

  &__img-label {}

  &__footer-wrap {
    display: flex; }

  &__footer-wrap:nth-child(1) {
    justify-content: space-between;
    margin-bottom: 73px; }

  &__submit-descr {
    font-weight: 400;
    font-size: 30px;
    line-height: 120%;
    margin-right: 40px;
    max-width: 260px; }

  &__footer-box:first-child {
    display: flex;
    column-gap: 26px; }

  &__footer-content {
    display: flex;
    justify-content: space-between; } }

.log-in-form {
  max-width: 667px;
  margin: 0 auto;

  &__wrap {
    margin-bottom: 10px;
    position: relative;
    .icon--check {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      width: 20px;
      height: 20px;
      fill: $black; } }

  &__wrap:nth-child(4) {
    margin-bottom: 44px; }

  &__wrap {
    .show-btn {
      cursor: pointer;
      user-select: none;
      .icon--eye {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        transition: all 0.3s ease;
        opacity: 0;
        visibility: hidden; }
      .icon--eye-slash {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        transition: all 0.3s ease; } }
    .show-btn.active {
      .icon--eye {
        opacity: 1;
        visibility: visible; }
      .icon--eye-slash {
        opacity: 0;
        visibility: hidden; } } }

  &__input {
    color: $black;
    font-weight: 500;
    font-size: 25px;
    border: none;
    border-bottom: 3px solid #FF4F00;
    background: transparent;
    padding-right: 30px;
    &::placeholder {
      color: $black;
      font-weight: 500;
      font-size: 25px; }
    &:focus {
      border-color: #51ff34;
      ~.icon--check {
        fill: #51ff34; } } }

  &__descr {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 32px;
    .icon--check {
      width: 20px;
      height: 20px;
      fill: $black; }
    span {
      font-weight: 500;
      font-size: 25px;
      color: $black;
      margin-left: 11px; } }

  &-submit {
    background: $accent-grnt;
    border: none;
    color: $white;
    font-weight: 700;
    font-size: 18px;
    line-height: 110%;
    border-radius: 10px;
    padding: 20px 32px;
    transition: all 0.3s ease;
    &:hover {
      color: darken($white, 10%); } } }
