// Typography

* {
  margin: 0;
  padding: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

a {
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus {
    text-decoration: none;
    outline: none; } }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

// Titles
h1, h2, h3, h4, h5, h6 {
	// font-family: inherit
	// line-height: 1.1
	// font-weight: 600
 }	// color: inherit

h1 {
  font-size: 36px; }
h2 {
  font-size: 30px; }
h3 {
  font-size: 24px; }
h4 {
  font-size: 18px; }
h5 {
  font-size: 14px; }
h6 {
  font-size: 12px; }

// Paragraphs
p {
	// margin-bottom: 1em
 }	// color: inherit

a {
  text-decoration: none; }

.title {
  font-weight: 400;
  font-size: 64px;
  line-height: 130%;
  color: $black;
  h1, h2, h3, h4, h5, h6 {
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit; } }

.wrapper {
  overflow: hidden;
  max-width: 100%;
  position: relative; }

.wrapper.main-bg {
  background-image: url(../assets/images/bg/main-bg.png);
  background-size: cover;
  background-repeat: no-repeat; }

