.header {
  padding: 78px 0 15px;

  .logo {
    display: inline-block;
    width: 243px;
    height: 73px;

    .icon--logo {
      fill: $black; } }

  &__content {
    display: flex;
    justify-content: space-between; }

  &__actions {
    display: flex;
    align-items: center; }

  &__search {
    margin-right: 14px;

    .icon--search {
      width: 24px;
      height: 24px; } }

  &__notice {
    margin-right: 26px;
    position: relative;

    .icon--notice {
      position: relative;
      width: 22px;
      height: 24px;
      z-index: 1; }

    span {
      position: absolute;
      top: 2px;
      left: 0;
      display: inline-block;
      width: 8px;
      height: 8px;
      background: #FF003D;
      border-radius: 50%;
      opacity: 0;
      visibility: hidden; } }

  &__notice.notice-active {
    span {
      opacity: 1;
      visibility: visible; } }

  &__profile {
    display: flex;
    align-items: center;

    .icon--arrow {
      width: 15px;
      height: 10px; } }

  &__profile-img {
    width: 38px;
    height: 38px;
    border-radius: 50%;
    margin-right: 15px;
    img {
      width: 100%;
      height: 100%; } }

  &__profile-name {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: $black;
    margin-right: 15px; } }

.wrapper.black-header {
  .header {
    background: $black;

    .logo {
      .icon--logo {
        fill: $white; } }

    &__search {
      .icon--search {
        fill: $white; } }

    &__notice {
      .icon--notice {
        fill: $white; } }

    &__profile {
      .icon--arrow {
        fill: $white; } }

    &__profile-name {
      color: $white; } } }
