.choose-stars {
  padding: 141px 0 91px;

  &__content {}

  &__images {
    display: flex;
    flex-direction: column;
    align-items: flex-end; }

  &__images-top {
    display: flex;
    column-gap: 20px;
    margin-bottom: 32px;

    .choose-stars__img:nth-child(1) {
      width: 384px;
      height: 181px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border-radius: 10px; } }

    .choose-stars__img:nth-child(2) {
      width: 183px;
      height: 181px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border-radius: 10px; } } }

  &__img {
    display: inline-block; }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .choose-stars__img {
      flex: 0 0 385px;
      height: 368px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border-radius: 10px; } } }

  &__title {
    font-weight: 400;
    font-size: 90px;
    line-height: 120%;
    // max-width: 740px
    display: inline-block; } }
