.news {
  padding-bottom: 83px;

  &__content {}

  &__title {
    font-weight: 400;
    font-size: 64px;
    line-height: 120%;
    margin-bottom: 29px; }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 22px;
    row-gap: 63px;
    margin-bottom: 90px; }

  &__item {}

  &__item-img {
    max-width: 384px;
    max-height: 265px;
    border-radius: 10px;
    margin-bottom: 10px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      object-fit: contain; } }

  &__item-link {
    display: inline-block;
    font-weight: 400;
    font-size: 26px;
    line-height: 37px;
    max-width: 378px;
    color: $black;
    margin-bottom: 30px;
    @include min_md {
      &:hover {
        color: lighten($black, 30%); } } }

  &__item-descr {
    display: flex;
    align-items: center;
    span {
      font-weight: 400;
      font-size: 16px;
      margin-right: 4px; }
    span:nth-child(1) {}
    span:nth-child(2) {
      color: #FF5C01; }
    span:nth-child(3) {
      display: inline-block;
      width: 25px;
      height: 1px;
      background: $black;
      margin: 0 13px; }
    span:nth-child(4) {} }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  &__more-link {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 31px;
    font-weight: 700;
    font-size: 20px;
    color: $black;
    white-space: nowrap;
    .icon--more-arrow {
      transition: all 0.3s ease;
      width: 23px;
      height: 14px;
      fill: $black; }
    @include min_md {
      &:hover {
        color: lighten($black, 25%);
        .icon--more-arrow {
          fill: lighten($black, 25%); } } } } }
