.congratulations {
  padding: 118px 0 137px;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }

  &__title {
    font-weight: 400;
    font-size: 64px;
    line-height: 120%;
    margin-bottom: 70px; }

  &__text {
    max-width: 1205px;
    margin-bottom: 71px;
    p {
      font-weight: 400;
      font-size: 40px;
      line-height: 120%;
      text-align: center; } }

  &__img {
    max-width: 838px;
    max-height: 538px;
    img {
      width: 100%;
      height: 100%; } } }
